export default {
	data() {
		return {
			order_no:'',
			result: false,
		}
	},
	onLoad(options){
		
	},
	methods: {
		inputChange(e){
			const key = e.currentTarget.dataset.key;
			this[key] = e.detail.value;
		},
		reQuery(){
			this.result = false;
		},
		search(){
			this.$http.post('/cms/member/order/input', {order_no:this.order_no}).then(res => {
				if(res.info.status==1){
					uni.showToast({
						title:res.info.status_err,
						duration:1500,
					})
				}
				else {
					uni.showToast({
						title:res.info.status_err,
						duration:1500,
					})
				}
				// this.result = true;
			}).catch(err => {});	
		}
	}
}
